<script>
import AppLogo from "./AppLogo.vue";
export default {
  name: "LeftPanel",
  components: { AppLogo },
  data() {
    return {
      today: [
        {
          name: "Table",
          active: true,
        },
        {
          name: "GPH contracts",
          active: false,
        },
        {
          name: "Statute",
          active: false,
        },
      ],
      yesterday: [
        {
          name: "Statute",
          active: true,
        },
      ],
      footer: [
        {
          name: "Legal Information",
          active: false,
          img: "legal-info",
        },
        {
          name: "Support",
          active: false,
          img: "support",
        },
      ],
      isLoading: true,
      more: false,
    };
  },
  methods: {
    navClick(name) {
      try {
        for (let i = 0; i < this.today.length; i++) {
          let item = this.today[i];
          if (item.name == name) {
            item.active = true;
          } else {
            item.active = false;
          }
          this.today[i] = item;
        }
      } catch (err) {
        this.$router.push({ name: "404" });
      }
    },

    checkRoute() {
      let name = this.$route.name;
      for (let i = 0; i < this.today.length; i++) {
        let card = this.today[i];
        if (card.route == name) {
          card.active = true;
          this.today[i] = card;
        }
      }
    },
  },
  async mounted() {
    // this.checkRoute();
  },
};
</script>
