      <div class="hr"></div>
      <div class="actions">
        <img src="../assets/switch.svg" alt="" />
        <div class="group-img">
          <img src="../assets/search.svg" alt="" />
          <img src="../assets/add-chat.svg" alt="" />
        </div>
      </div>
      <nav class="group-nav">
        <h2>Today</h2>
        <li
          class="nav-item"
          @click="navClick(item.name)"
          v-for="item in today"
          :key="item"
          :class="{ active: item.active }"
        >
          <div class="nav-item-info">
            <img
              v-if="item.active"
              src="../assets/message-menu-active.svg"
              alt=""
            />
            <img v-if="!item.active" src="../assets/message-menu.svg" alt="" />
            <span :class="{ blue: item.active }">{{ item.name }}</span>
          </div>
          <img src="../assets/more-chat.svg" v-if="item.active" alt="" />
        </li>
      </nav>
    </div>
    <div class="footer">
      <div class="hr"></div>
      <nav class="group-nav">
        <li
          class="nav-item"
          @click="navClick(item.name)"
          v-for="item in footer"
          :key="item.name"
          :class="{ active: item.active }"
        >
          <div class="nav-item-info">
            <img :src="`/assets/${item.img}.svg`" alt="" />
            <span>{{ item.name }}</span>
          </div>
        </li>
      </nav>
      <div class="miniHR"></div>
      <div class="wrap-profile">
        <div class="profile">
          <div class="avatar">
            <img src="../assets/image.png" alt="" />
          </div>
          <div class="profile-info">
            <span class="name">Alexander A.</span>
            <span class="email">alexeev@gmail.com</span>
          </div>
        </div>
        <div class="more">
          <img @click="more = !more" src="../assets/more.svg" alt="" />
          <div class="options" v-if="more">
            <button>Account</button>
            <button class="delete">Log out</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
